.wrapper {
   font-family: 'Public Sans';
   display: flex;
   flex-direction: column;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul,
p {
   padding: 0;
   margin: 0;
}

.container {
   max-width: 1182px;
   margin: 0 auto;
   padding: 0px 15px;
}

a,
li {
   text-decoration: none;
   list-style-type: none;
}

.header {
   padding: 17px 0px;
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;

   // .header__container

   &__container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 20px;
   }

   // .header__picture

   &__picture {
      position: relative;
      z-index: 3;
   }

   // .header__logo

   &__logo {}

   // .header__img

   &__img {}

   // .header__menu

   &__menu {
      flex: 0 1 50%;

      @media (max-width: 890px) {
         flex: 0 1 100%;
      }
   }

   &__button {
      display: none;

      @media (max-width: 570px) {
         position: relative;
         display: block;
         z-index: 5;
         width: 30px;
         height: 30px;
         border-radius: 100%;
         background: rgb(211, 50, 50);
         border: none;
         margin-left: auto;
         transition: all 0.3s ease 0s;

         &.button-open {
            background: #0096db;
            border-radius: 0%;
         }
      }
   }

   // .header__items

   &__items {
      display: flex;
      justify-content: space-between;
      gap: 20px;
      font-size: 13px;
      line-height: 16px;

      @media (max-width: 570px) {
         position: fixed;
         top: 0;
         right: -100%;
         width: 100%;
         height: 100%;
         flex-direction: column;
         align-items: center;
         justify-content: flex-start;
         background: #000;
         padding: 100px 0px 50px;
         font-size: 30px;
         line-height: 34px;
         overflow: auto;
         z-index: 2;
         transition: right 0.3s ease 0s;

         &::before {
            content: '';
            position: fixed;
            top: 0;
            right: -100%;
            width: 100%;
            height: 60px;
            background: #000;
            transition: right 0.3s ease 0s;
         }

         &.menu-open {
            right: 0;


            &::before {
               right: 0;
            }

         }
      }
   }

   // .header__item

   &__item {}

   // .header__link

   &__link {
      color: #fff;
      transition: all 0.3s ease 0s;

      &:hover {
         color: rgb(175, 175, 175);
      }
   }
}



