.step {
   padding: 115px 0px 141px;
   background: #fff;
   // .step__container

   &__container {
      display: flex;
      flex-direction: column;
      align-items: center;
   }

   // .step__title

   &__title {
      font-family: 'Libre Baskerville';
      font-style: normal;
      font-weight: 400;
      font-size: 30px;
      line-height: 38px;
      text-align: center;
      letter-spacing: 1.2px;
      color: #003331;
      margin-bottom: 62px;
      max-width: 717px;

      @media (max-width: 500px) {
         font-size: 20px;
         line-height: 25px;
      }
   }

   // .step__items

   &__items {
      display: flex;
      justify-content: space-between;
      gap: 20px;
      width: 770px;
      position: relative;

      &::before {
         content: '';
         width: 100%;
         height: 1px;
         background: rgba(53, 83, 98, 0.2);
         position: absolute;
         top: 30%;
         left: 0;
      }

      @media (max-width: 800px) {
         width: 100%;
      }

      @media (max-width: 500px) {
         flex-wrap: wrap;
         justify-content: center;

         &::before {
            display: none;
         }
      }
   }

   // .step__item

   &__item {
      display: flex;
      flex-direction: column;
      align-items: center;
      background: #fff;
      position: relative;
   }


   &__wrapper {
      width: 48px;
      height: 48px;
      background: #014C48;
      border-radius: 999px;
      display: block;
      position: relative;
      margin-bottom: 19px;
   }

   // .step__number

   &__number {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: #fff;
   }

   // .step__text

   &__text {
      max-width: 126px;
      text-align: center;
   }
}

