.press {
   background: #fff;
   padding: 87px 0px 132px;
   // .press__container

   &__container {}

   // .press__body

   &__body {
      display: flex;
      gap: 20px;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 45px;

      @media (max-width:550px) {
         flex-wrap: wrap;
      }
   }

   // .press__title

   &__title {
      font-family: 'Libre Baskerville';
      font-style: normal;
      font-weight: 400;
      font-size: 27px;
      line-height: 38px;
      letter-spacing: 1.2px;
      color: #003331;
   }

   // .press__button

   &__button {
      border: 1px solid rgba(53, 83, 98, 0.2);
      color: #00413E;
      margin-left: auto;
   }

   // .press__items

   &__items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-auto-rows: 1fr;
      gap: 50px;

      @media (max-width: 780px) {
         grid-template-columns: repeat(2, 1fr);
      }

      @media (max-width: 550px) {
         grid-template-columns: repeat(1, 1fr);
      }
   }

   // .press__item

   &__item {
      position: relative;

      &::before {
         content: '';
         width: 100%;
         height: 1px;
         background: rgba(53, 83, 98, 0.3);
         position: absolute;
         top: -20px;
         left: 0;
      }
   }

   // .press__block

   &__block {
      margin-bottom: 11px;
      display: flex;
      gap: 16px;
      font-size: 11px;
      line-height: 16px;
      letter-spacing: 0.5px;
      text-transform: uppercase;
      color: rgba(53, 83, 98, 0.6);
   }

   // .press__text

   &__text {
      position: relative;
      padding-left: 30px;

      &::before {
         content: '';
         width: 24px;
         height: 24px;
         position: absolute;
         top: -4px;
         left: 0;
      }

      // .press__text_v1

      &_v1 {
         &::before {
            background: url('../images/press/ted.svg') no-repeat;
         }
      }

      // .press__text_v2

      &_v2 {
         &::before {
            background: url('../images/press/correctional_news.svg') no-repeat;
         }
      }

      // .press__text_v3

      &_v3 {
         &::before {
            background: url('../images/press/department_of_justice.svg') no-repeat;
         }
      }

      // .press__text_v4

      &_v4 {
         &::before {
            background: url('../images/press/the_new_york_times.svg') no-repeat;
         }
      }

      // .press__text_v5

      &_v5 {
         &::before {
            background: url('../images/press/corrections1.svg') no-repeat;
         }
      }

      // .press__text_v6

      &_v6 {
         &::before {
            background: url('../images/press/right_on_crime.svg') no-repeat;
         }
      }
   }

   // .press__data

   &__data {}

   // .press__link

   &__link {
      font-size: 17px;
      line-height: 25px;
      color: #00413E;

      &:hover {
         text-decoration: underline;
      }
   }
}

